const reducer = (state = initialState, action) => {
    switch (action.type) {
        case "ADD_DISH":
            toast.success("  ", { theme: "colored", position: toast.POSITION.BOTTOM_RIGHT, });
            return {
                ...state,
                cart: [...state.cart, action.payload],

            };
        case "DELETE_DISH":
            return {
                ...state,
                cart: state.cart.filter((item) => item.id !== action.payload),
            };
        case "CLEAN_CART":
            return {
                ...state,
                cart: []
            }
        default:
            return state
    };
}

export default reducer;