import Tooltip from '@mui/material/Tooltip';
import styles from "./styles.module.css"


const Menu = () => {
    const menu = [
        { title: "", link: "#exposition", text: "" },
        { title: "", link: "#prologue", text: "' " },
        { title: " ", link: "#culmination", text: "" },
        { title: " ", link: "#end", text: "'" },
        { title: "", link: "#epilogue", text: "" },
    ]

    return (
        <div className={styles.menu}>
            {menu.map((item) => {
                return <Tooltip title={item.title} placement="bottom" arrow
                    componentsProps={{
                        tooltip: {
                            sx: {
                                fontSize: '18px',
                                backgroundColor: "black",
                            },
                        },
                    }}>
                    <a className={styles.menu_item} href={item.link}>{item.text}</a>
                </Tooltip>
            })}
        </div>

    );
}

export default Menu;